* {
    font-family: 'RalewayVariable', sans-serif;
}
.topHeader {
    display: flex;
    justify-content:space-between;
    align-items: center;
    background-color:rgb(248, 255, 255);
    height: 130px;
    width: 100%;
}

.topHeader img {
    width: 6%;
    height: auto;
    margin-right: 10%;
    /* top: 30px;
    right: 10%; */
}
.topHeader h1 {
    margin: auto;
    padding-left: calc(15%);
    width: 25%;
    text-align: center;
    box-sizing: content-box;
    font-size: 2vw;
}

@media (max-width: 768px) {
    .topHeader h1 {
      font-size: 20px;
    }
  }

.bottomHeader {
    width: 100%;
    height: 40px;
    background-color: #ffe731;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}
