.FileUploaderBlock {
    width: 80%;
    height: 540px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.FileUploaderPdfBlock {
    width: 40%;
    height: 400px;
    background-color:aliceblue;
    border: 1px solid #222;
    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;
}

.pdfView {
    width: 400px;
    height: 400px;
    align-items: center;
}

.FileUploaderInput {
    height: 50px;
    font-size: 36px;
    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'RalewayVariable', sans-serif;
}

.FileUploaderPdfBlock object {
    border: 2px solid #222;
}

.workButtonBlock {
    height: 70px;
    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;
}

.workButtonBlock button {
    margin-top: 10px;
    text-decoration: none;
    display: inline-block;
    width: 140px;
    height: 45px;
    line-height: 45px;
    border-radius: 45px;
    margin: 10px 20px;
    font-family: 'RobotoBolt', sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #222;
    background: white;
    box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
    transition: .3s;
  }

.workButtonBlock button:hover {
    background: #ffe731;
    transform: translateY(-5px);
}

.FileUploaderResultBlock {
    height: 50px;
    font-family: 'RobotoBolt', sans-serif;
    font-size: 16px;
    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;
}
